<h1 id="商户信息修改" tabindex="-1"><a class="header-anchor" href="#商户信息修改" aria-hidden="true">#</a> 商户信息修改</h1>
<h2 id="修改结算银行卡" tabindex="-1"><a class="header-anchor" href="#修改结算银行卡" aria-hidden="true">#</a> 修改结算银行卡</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$response</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">material</span><span class="token operator">-></span><span class="token function">setSettlementCard</span><span class="token punctuation">(</span><span class="token punctuation">[</span>
    <span class="token comment">// 'sub_mch_id' => '1230000109',</span>
    <span class="token string single-quoted-string">'account_number'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'银行卡号'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'bank_name'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'开户银行全称（含支行）'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'account_bank'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'开户银行'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'bank_address_code'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'开户银行省市编码'</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><h2 id="修改联系信息" tabindex="-1"><a class="header-anchor" href="#修改联系信息" aria-hidden="true">#</a> 修改联系信息</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$response</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">material</span><span class="token operator">-></span><span class="token function">updateContact</span><span class="token punctuation">(</span><span class="token punctuation">[</span>
    <span class="token comment">// 'sub_mch_id' => '1230000109',</span>
    <span class="token string single-quoted-string">'mobile_phone'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'手机号'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'email'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'邮箱'</span><span class="token punctuation">,</span>
    <span class="token string single-quoted-string">'merchant_name'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'商户简称'</span><span class="token punctuation">,</span>
<span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><blockquote>
<p>以上接口调用过 <code>setSubMchId</code> 方法则无需传入 <code>sub_mch_id</code> 参数</p>
</blockquote>
